import React, { Component } from 'react'
import './../testcss/Slidingbar.css';
export class slidingbar extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       isShow : false
    }
    this.toggleshow = this.toggleshow.bind(this);
  }
  toggleshow(){
   this.setState(prevState => ({
     isShow : !prevState.isShow
   }))
  };
 
  render() {
    const className = this.state.isShow ? "expand" : "none-expand";

     
    const button_toggle = (
      <div className="toggle-wrapper" >
        <a className="toggle-button" onClick={this.toggleshow} >
          <span className="screen-reader-text sign-toggle" >{this.state.isShow ? '-' : '+'}</span>
          
        </a>
      </div>
    );
    const toggle_content = (
      
          <div className= { this.state.isShow ? "expand" : "none-expand" }>
        <div className="row ">
          <div className="first-element col-lg-4 col-xl-4 col-md-4">
            <div className="s-title">
            <span >SLIDING BAR SECTION FOR WIDGETS</span>
            </div>
            
            <div className="l-content">
            <span className="sliding-text">
              <p>This is the sliding section that can be enabled or disabled on any demo. You can insert anywhere from 1-6 columns along with any widget that comes with Avada, WordPress or additional 3rd party plugins. It's perfect for placing additional information on your site.</p>
            </span>
              
            </div>
          </div>
          <div className="second-element col-lg-4 col-xl-4 col-md-4">
            <div className="s-title">
            <span>RECENT WORK</span>
            </div>
            <div className="img-work">
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/donec-et-sagittis/">
                  <img src=" https://avada.theme-fusion.com/wp-content/uploads/2014/09/port21-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/enim-pellentesque/">
                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port12-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/cras-mollis-nons/">
                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port18-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/proin-tortor-orcus/">
                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port19-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/cras-commodo-ets/">
                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port15-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/class-et-aptent/">
                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port11-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/iaculis-eu-gigni/">

                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port10-66x66.jpg" />
                </a>
              </div>
              <div className="img-item-work">
                <a href="https://avada.theme-fusion.com/portfolio-items/donec-magna-velit/">
                  <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port17-66x66.jpg" />
                </a>
              </div>
            </div>
          </div>
          <div className="third-element col-lg-4 col-xl-4 col-md-4">
            
              <div className="s-title">
                <span >CONTACT INFO</span>
              </div>
              <div className="contact-info-container">

                <p className="address">12345 North Street, New York City, NY 555555</p>
                <p className="phone">Phone: 1-800-555-5555</p>
                <p className="mobile">Mobile: 1-234-567-8910</p>
                <p className="fax">Fax: 1-234-879-5641</p>
                <p className="email">Email: <a href="mailto:info@yourwebsite.com">info@yourwebsite.com</a></p>
                <p className="web">Web: <a href="http://yourwebsite.com">
                  http://yourwebsite.com </a></p>
              </div>
          
          </div>
        </div>
        </div>
       
    );
    
    return (
      <div className=" slidingbar-top-bar sliding-top-bar-area fusion-sliding-bar">
       
         
      {
        this.state.isShow ? toggle_content : ''
      }
      {button_toggle}
      </div>
      
    )
  }
}

export default slidingbar
