import React, { Component } from 'react'
import './../testcss/banner.css';
export class banner extends Component {
  render() {
    return (
      <div className="fushion-fullwith">
        
            <div className = "fushion-builder-row fushion-row">
                <div className="parallax-inner"></div>
                <div className="fushion-text">
                <h1> <span className="banner-title">#1 Selling Theme of All Time</span></h1>
                </div>
               
                
                <div className="imageframe-align-center">
                    <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/not_required1.png" height = "60"/>
                </div>
                <div className="fusion-imageframe1">
                    <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/avada_build_unique.png"/>
                </div>
            </div>
        
      </div>
    )
  }
}

export default banner
