import React, { Component } from 'react'
import '../testcss/DropDownarrow.css';
export class DropDownArrow extends Component {
  render() {
    return (
      <div className="dropdown-arrow-container">
            <div className="fusion-builder-row fusion-row">
                <div className="dropdown-arrow-wrapper">
                    <div className="arrow-details">
                        <div className="arrow-triangle">
                            <div className="divider-candy-arrow bottom"></div>
                            <div className="divider-candy bottom"></div>
                        </div>
                    </div>
                </div>
            </div>
      </div>
    )
  }
}

export default DropDownArrow
